import maps from './charMaps';

// Based on code from https://gitlab.sas.com/htmlcommons-buildtools/sas-pseudol10n4js

/**
 * @type {Array} Mapping table for expansion characters
 * @ignore
 */
// prettier-ignore
const expMap = [
  0, 0, 1, 2, 3, 3, 3, 3, 4, 4,
  4, 5, 5, 5, 6, 6, 7, 7, 8, 8,
  8, 8, 8, 8, 8, 8, 8, 8, 8, 8,
  8, 8, 9, 9, 9, 9, 9, 9, 9, 10,
  10, 10, 10, 10, 11, 11, 11, 11, 12, 12,
  12, 12, 12, 12, 12, 12, 12, 12, 12, 12,
  12, 12, 12, 12, 12, 12, 12, 12, 12, 12,
  12, 12, 12, 12, 12, 12, 13, 13, 13, 13,
  13, 13, 13, 13, 14, 14, 14, 14, 14, 14,
];

type IgnoreFnType = ((char: string) => boolean | number) | null;

/**
 * @type {Map} Mapping table for characters to ignore
 * @ignore
 */
const ignoreMap: {
  [char: string]: IgnoreFnType;
} = {
  // html
  '<': (char: string) => char === '>',
  // html
  '{': (char: string) => char === '}',
  // macro function
  '%': (char: string) => ~[' ', ',', ':', ';', '?', '!', ')', '}', ']'].indexOf(char),
  // macro variable, html entities
  '&': (char: string) => ~[' ', ';'].indexOf(char),
};

interface Strategy {
  prefix: string;
  suffix: string;
  map: {
    [char: string]: string;
  } | null;
  extChar: string;
  quote: string;
  locale: string;
  ratio: number;
}

/**
 * @type {Map} Mapping table for strategies to pseudo-translate
 * @ignore
 */
const pl_xx_Strategy: Strategy = {
  prefix: '[',
  suffix: ']',
  map: maps.pl_pseudoCharMap,
  extChar: '@',
  quote: "'",
  locale: 'pl_XX',
  ratio: 1,
};
const zh_cn_pseudo_Strategy: Strategy = {
  prefix: 'Ｘ',
  suffix: 'Ｚ',
  map: null,
  extChar: '补',
  quote: "'",
  locale: 'zh_CN_pseudo',
  ratio: 1.25,
};
const ar_xx_Strategy: Strategy = {
  prefix: '',
  suffix: '',
  map: maps.ar_pseudoCharMap,
  extChar: '',
  quote: '',
  locale: 'ar_XX',
  ratio: 1,
};

/**
 * Returns the strategies options table
 *
 * @param {string} strategy
 * @return {object}
 */
export const getStrategy = (strategy: string): Strategy => {
  strategy = strategy.toLowerCase();
  let ps;
  switch (strategy) {
    case 'pl-xx':
      ps = pl_xx_Strategy;
      break;
    case 'zh-cn-pseudo':
      ps = zh_cn_pseudo_Strategy;
      break;
    case 'ar-xx':
      ps = ar_xx_Strategy;
      break;
    default:
      ps = pl_xx_Strategy;
      break;
  }
  return ps;
};

const placeholderRegex = /\{[A-Za-z0-9_]+}/g;
/**
 * Indicates if a message contains placeholder
 *
 * @param {String} strategy
 * @return {boolean}
 */
const hasPlaceholder = (str: string) => {
  return str.match(placeholderRegex);
};

/**
 * Returns the expansion string
 *
 * @return {String}
 */
const getExtension = function(len: number, ratio: number, extChar: string, locale: string) {
  let i: number;
  let extstr = '';
  let exp = len <= expMap.length ? expMap[len] : len * 0.15;
  exp = Math.floor(exp / ratio);
  if (exp === 0 || extChar.length === 0) return extstr;
  if (locale === 'zh_CN_pseudo') {
    for (i = 0; i < exp; i++) {
      if (i > 0 && i % 7 === 0) extstr += ' ';
      else extstr += extChar;
    }
  } else {
    const array = [];
    for (i = 0; i < exp; i++) {
      array.push(extChar);
    }
    for (i = 6; i < exp; i += 7) {
      array[i] = ' ';
    }
    extstr = array.join('');
  }
  return extstr;
};

/**
 * Returns the transcoded string according to the mapping table
 *
 * @param {Strategy} strategy
 * @param {string} str
 * @return {string}
 */
const transformString = (strategy: Strategy, str: string) => {
  if (!str || str.length === 0) {
    return str;
  }
  const charMap = strategy.map;
  const quote = hasPlaceholder(str) ? strategy.quote + strategy.quote : strategy.quote;

  const transformReducer = (
    accumulatedResult: { ignoreFn: IgnoreFnType; result: string; charCount: number },
    char: string
  ) => {
    let { ignoreFn, result, charCount } = accumulatedResult;
    // if we can stop ignoring
    if (ignoreFn && ignoreFn(char)) {
      ignoreFn = null;
    }
    if (!ignoreFn) {
      // if we need to start ignoring
      ignoreFn = ignoreMap[char];
      if (!ignoreFn) {
        charCount += 1;
        if (charMap !== null && charMap[char]) {
          // console.log(`replacing ${char} with ${charMap[char]}`);
          char = charMap[char];
        }
      } else {
        charCount -= 1;
      }
    }
    result += char;
    return { ignoreFn, result, charCount };
  };

  // replace escaped characters: \\n \\t \\r \\uXXXX
  str = str.replace(/\\[nrt]|\\u([0-9A-Fa-f]{4})/g, '%$&!');
  const { result, charCount } = str
    .split('')
    .reduce(transformReducer, { ignoreFn: null as IgnoreFnType, result: '', charCount: 0 });
  // put escaped character back in the string
  const res2 = result.replace(/\%(\\[nrt]|\\u([0-9A-Fa-f]{4}))\!/g, (match) => {
    return match.substr(1).slice(0, -1);
  });
  const res =
    strategy.prefix +
    res2 +
    quote +
    getExtension(charCount, strategy.ratio, strategy.extChar, strategy.locale) +
    strategy.suffix;
  return res;
};

/**
 * Pseudo-localize a string using a given strategy
 *
 * @param {string} strategy
 * @return {string}
 */
const pseudoLocalizeWith = (strategy: Strategy, str: string) => {
  const result = transformString(strategy, str);
  return result;
};

/**
 * Pseudo-localize a string for the locale
 *
 * @param {string} name Name of locale
 * @param {string} str The string to pseudo-localize
 * @return {string}
 */
export const pseudoLocalize = (name: string, str: string) => {
  if (str.length < 1) return str;
  const strategy = getStrategy(name);
  return pseudoLocalizeWith(strategy, str);
};
